import {
	mapState,
	mapMutations
} from 'vuex';
import bottomCopyright from '@/components/bottomCopyright/bottomCopyright.vue'
export default {
	components: {
		bottomCopyright
	},
	data() {
		return {
			iconList: [{
					icon: 'formfill',
					color: 'red',
					badge: 0,
					name: '我报名的',
					nav: 'list'
				}, {
					icon: 'peoplelist',
					color: 'yellow',
					badge: 1,
					name: '招聘人员',
					nav: 'acceptPeo'
				}, {
					icon: 'formfill',
					color: 'green',
					badge: 3,
					name: '我发布的',
					nav: 'mypush'
				},
				{
					icon: 'peoplefill',
					color: 'mauve',
					badge: 3,
					name: '个人中心',
					nav: 'userInfo'
				}, {
					icon: 'copy',
					color: 'cyan',
					badge: 3,
					name: '用户协议',
					nav: 'agreement'

				}, {
					icon: 'questionfill',
					color: 'blue',
					badge: 3,
					name: '常见问题',
					nav: 'freQuestion'
				}, {
					icon: 'communityfill',
					color: 'purple',
					badge: 3,
					name: '意见反馈',
					nav: 'feed'
				}, {
					icon: 'servicefill',
					color: 'purple',
					badge: 3,
					name: '联系客服',
					nav: 'contactService'
				}, {
					icon: 'friendfill',
					color: 'mauve',
					badge: 3,
					name: '关于我们',
					nav: 'aboutUs'
				}
			],
			curUserInfo: {},
			curWXInfo: null,
			curUserType: null,
			curData: {
				peo: 0,
				donecount: 0,
				fabucount: 0
			},
			msgContents: ['15327378160', '16671064940']
		};
	},
	computed: {
		...mapState(['globalOpenid', 'GlobalUserInfo', 'token', 'globalWXuserInfo', 'globalUserType'])
	},
	onLoad() {
		console.log(this.curWXInfo)
		var that = this;
		that.curUserType = that.globalUserType;
	},
	onShow() {
		var that = this;
		if (that.GlobalUserInfo.id) {
			that.getPerData();
			that.initData();
		}
	},
	methods: {
		// 跳转到哪里
		navWhere(e) {
			var that = this;
			if (that.GlobalUserInfo.id) {
				switch (e.nav) {
					case 'feed':
						uni.navigateTo({
							url: '/pages/public/uploadFeed/uploadFeed'
						})
						break;
					case 'userInfo':
						that.seeMyInfo();
						break;
					case 'mypush':
						that.navMyPublic();
						break;
					case 'aboutUs':
						that.navAboutUs('关于我们');
						break;
					case 'freQuestion':
						that.navAboutUs('常见问题');
						break;
					case 'agreement':
						that.navAboutUs('用户协议');
						break;
					case 'acceptPeo':
						that.navAcceptPeople();
						break;
					case 'contactService':
						that.chooseMsg();
						break;
					case 'list':
						that.navStuOrderList();
						break;
					default:
						break;
				}
			} else {
				uni.showModal({
					title: '提示',
					content: '您还未登录，请登录查看',
					success: function(res) {
						if (res.confirm) {
							uni.navigateTo({
								url: '/pages/login/login'
							});
						} else if (res.cancel) {}
					}
				});
			}
		},
		// 客服电话
		chooseMsg() { //快速输入
			uni.showActionSheet({
				itemList: this.msgContents,
				success: (res) => {
					uni.makePhoneCall({
						phoneNumber: this.msgContents[res.tapIndex]
					});
				}
			})
		},
		// 以招聘人员
		navAcceptPeople() {
			var that = this;
			var tempData = {
				id: that.GlobalUserInfo.id
			}
			var tempList = [];
			uni.request({
				url: that.global_baseUrl + '/index.php/seltaskstu',
				data: tempData,
				method: "POST",
				success: (res) => {
					console.log(res)
					if (res.data.code == '200') {
						res.data.data.forEach((item, index) => {
							item.forEach((item2, index2) => {
								tempList.push(item2);
							})
						})
						if (tempList.length > 0) {
							uni.navigateTo({
								url: '/pages/public/acceptPeople/acceptPeople?data=' + JSON.stringify(tempList),
							})
						}
					}
				},
			});
		},
		// 关于我们用户协议常见问题  三个富文本
		navAboutUs(text) {
			uni.navigateTo({
				url: '/pages/public/aboutUs/aboutUs?title=' + text
			})
		},

		// 我发布的
		navMyPublic() {
			uni.navigateTo({
				url: '/pages/myUser/myPush/myPush'
			})
		},
		initData() {
			uni.showLoading({
				title: '数据初始化中'
			})
			var that = this;
			that.curUserInfo = that.GlobalUserInfo;
			that.curWXInfo = that.globalWXuserInfo;
			setTimeout(() => {
				uni.hideLoading();
			}, 700)
		},
		async getPerData() {
			var that = this;
			var apiName = 'selidshop'
			if (that.globalUserType == 0) {
				apiName = 'selidstudent'
			}
			var tempData = {
				id: that.GlobalUserInfo.id
			}
			uni.request({
				url: that.global_baseUrl + '/index.php/' + apiName,
				data: tempData,
				method: "POST",
				success: (res) => {
					if (res.data.code == '200') {
						that.curData = res.data.data;
					} else {

					}
				}
			});
		},
		seeMyInfo(e) {
			var that = this;
			if (that.GlobalUserInfo.id) {
				uni.navigateTo({
					url: '/pages/public/userInfo/userInfo'
				})
			} else {
				uni.showModal({
					title: '提示',
					content: '您还未登录，请登录查看',
					success: function(res) {
						if (res.confirm) {
							uni.navigateTo({
								url: '/pages/login/login'
							});
						} else if (res.cancel) {}
					}
				});
			}
		},
		navStuOrderList() {
			uni.navigateTo({
				url: '/pages/myUser/myOrderList/myOrderList'
			})
		},
		pushNew() {
			uni.navigateTo({
				url: '/pages/public/pushTask/pushTask'
			})
		},
	}
}
